import { UserDetails, FetchedUser } from "types";


const personalities =[
    'Jestem programist z Warszawy, mwi po polsku zwile i konkretnie. Rzucam przykadami kodu i uywam list punktowych, jakbymy siedzieli przy kawie w tech startupie.',
    'Jestem piratem z Gdaska, co opowiada po polsku z morskim zadziorem! Argh! Rzucam arty, jakbymy byli na pokadzie aglowca przy flaszy rumu.',
    'Jestem nauczycielem z Krakowa, tumacz po polsku prosto i z sercem, jakbym uczy kumpla w kawiarni. Uywam prostych analogii, eby wszystko byo jasne.',
    'Jestem komikiem z dzkiej sceny, mwi po polsku z sarkazmem i cit ripost. Rzucam arty, ale zawsze daj konkret, jakbymy byli na stand-upie.',
    'Jestem konsultantem z Wrocawia, odpowiadam po polsku formalnie i uprzejmie, jak na spotkaniu biznesowym. Moje odpowiedzi s jasne i zorganizowane.',
    'Jestem trenerem motywacyjnym z Poznania, mwi po polsku z energi i pasj! Rzucam inspirujce cytaty, jakbymy szykowali si na maraton sukcesu!',
    'Jestem inynierem z przyszoci, mwi po polsku w stylu sci-fi, jakbymy byli na stacji kosmicznej. Opowiadam o tech cudach i gwiazdach!',
    'Jestem historykiem z Lublina, mwi po polsku jak gawdziarz, czc fakty z anegdotami, jakbymy siedzieli przy ognisku w zamku.',
    'Jestem podrnikiem z Mazur, mwi po polsku z pasj do przygd. Opowiadam o wiecie, jakbymy byli w drodze na kolejny szlak!',
    'Jestem filozofem z Torunia, mwi po polsku refleksyjnie, z metaforami i cytatami, jakbymy dyskutowali o yciu przy gwiazdach.'
]
function getData(data: string | null, replacement: string = "Undisclosed") {
    return data || replacement;
}

function createUserDetails(obj: FetchedUser): UserDetails {
    const array = new Uint32Array(1);
    window.crypto.getRandomValues(array);

    const result = {
        dob: getData(obj?.dob?.date),
        phone: getData(obj?.phone),
        name: { first: getData(obj?.name?.first, ""), last: getData(obj?.name?.last, "") },
        location: { city: getData(obj.location?.city), country: getData(obj.location?.country) },
        isFavorite: false,
        picture: obj.picture,
        nat: getData(obj?.nat, ""),
        id: array[0].toString(),
        social: { facebook: "", linkedin: "", twitter: "" },
        isOnline: Boolean(Math.round(Math.random())),
        personality: personalities[Math.floor(Math.random() * personalities.length)],
    };

    return result;
}

export function createUsers(ary: FetchedUser[]) {
    return ary.map(createUserDetails);
}

export default createUsers;
